@media screen and (min-width: 600px) {
  .z-article-content .z-article-body {
    flex: 1 1;
    display: flex;
  }
  .z-article-content .z-article-body .z-article-left {
    flex: 1 1;
    border-right: 1px solid #eaecef;
  }
  .z-article-content .z-article-body .z-article-right {
    flex: 1 1;
  }
}
.z-article-content {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.z-article-content .z-article-header {
  height: 3.5rem;
  line-height: 3.5rem;
  box-sizing: border-box;
  border-bottom: 1px solid #eaecef;
}
.z-article-content .z-article-left,
.z-article-content .z-article-right {
  padding: 1rem;
}
.z-markdown {
  color: #2c3e50;
}
.z-markdown a {
  font-weight: 500;
  text-decoration: none;
}
.z-markdown a,
.z-markdown p a code {
  color: #2486ff;
}
.z-markdown h1,
.z-markdown h2,
.z-markdown h3,
.z-markdown h4,
.z-markdown h5,
.z-markdown h6 {
  font-weight: 600;
  line-height: 1.25;
  margin-bottom: 1rem;
}
.z-markdown h1 {
  font-size: 2.2rem;
}
.z-markdown h2 {
  font-size: 1.65rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid #eaecef;
}
.z-markdown h3 {
  font-size: 1.35rem;
}
.z-markdown code,
.z-markdown kbd {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}
.z-markdown p a code {
  font-weight: 400;
}
.z-markdown ol,
.z-markdown p,
.z-markdown ul {
  line-height: 1.7;
}
.z-markdown ol,
.z-markdown ul {
  padding-left: 1.2em;
}
.z-markdown blockquote > p {
  margin: 0;
}
.z-markdown blockquote {
  font-size: 1rem;
  color: #999;
  border-left: 0.2rem solid #dfe2e5;
  margin: 1rem 0;
  padding: 0.25rem 0 0.25rem 1rem;
}
.z-markdown hr {
  border: 0;
  border-top: 1px solid #eaecef;
}
.z-markdown table {
  border-collapse: collapse;
  margin: 1rem 0;
  display: block;
  overflow-x: auto;
}
.z-markdown tr {
  border-top: 1px solid #dfe2e5;
}
.z-markdown tr:nth-child(2n) {
  background-color: #f6f8fa;
}
.z-markdown td,
.z-markdown th {
  border: 1px solid #dfe2e5;
  padding: 0.6em 1em;
}
.z-markdown code[class*=language-] {
  position: relative;
  margin: 1rem 0;
  border-radius: 6px;
  padding: 1.25rem 1.5rem;
}
.z-markdown code[class*=language-]:before {
  position: absolute;
  z-index: 3;
  top: 0.8em;
  right: 1em;
  font-size: 0.75rem;
  color: #2c3e50;
}
.z-markdown code[class~=language-js]:before {
  content: "js";
}
.z-markdown code[class~=language-ts]:before {
  content: "ts";
}
.z-markdown code[class~=language-html]:before {
  content: "html";
}
.z-markdown code[class~=language-md]:before {
  content: "md";
}
.z-markdown code[class~=language-vue]:before {
  content: "vue";
}
.z-markdown code[class~=language-css]:before {
  content: "css";
}
.z-markdown div[class~=language-sass]:before {
  content: "sass";
}
.z-markdown code[class~=language-scss]:before {
  content: "scss";
}
.z-markdown code[class~=language-less]:before {
  content: "less";
}
.z-markdown code[class~=language-stylus]:before {
  content: "stylus";
}
.z-markdown code[class~=language-go]:before {
  content: "go";
}
.z-markdown code[class~=language-java]:before {
  content: "java";
}
.z-markdown code[class~=language-c]:before {
  content: "c";
}
.z-markdown code[class~=language-sh]:before {
  content: "sh";
}
.z-markdown code[class~=language-yaml]:before {
  content: "yaml";
}
.z-markdown code[class~=language-py]:before {
  content: "py";
}
.z-markdown code[class~=language-docker]:before {
  content: "docker";
}
.z-markdown code[class~=language-dockerfile]:before {
  content: "dockerfile";
}
.z-markdown code[class~=language-makefile]:before {
  content: "makefile";
}
.z-markdown code[class~=language-javascript]:before {
  content: "js";
}
.z-markdown code[class~=language-typescript]:before {
  content: "ts";
}
.z-markdown code[class~=language-markup]:before {
  content: "html";
}
.z-markdown code[class~=language-markdown]:before {
  content: "md";
}
.z-markdown code[class~=language-json]:before {
  content: "json";
}
.z-markdown code[class~=language-ruby]:before {
  content: "rb";
}
.z-markdown div[class~=language-python]:before {
  content: "py";
}
.z-markdown code[class~=language-bash]:before {
  content: "sh";
}
.z-markdown code[class~=language-php]:before {
  content: "php";
}
