.z-textarea textarea {
  width: 100%;
  box-sizing: border-box;
  line-height: 20px;
  padding: 9px 8px;
  border: 1px solid #d0d0d5;
  border-radius: 4px;
  background-color: #fff;
  outline: 0;
  color: #2c3e50;
  font-size: 14px;
  -webkit-transition: border-color 0.15s, background-color 0.15s;
  transition: border-color 0.15s, background-color 0.15s;
  word-break: break-all;
  vertical-align: top;
  resize: none;
  overflow: auto;
}
.z-textarea textarea:focus {
  border-color: #2486ff;
}
